<template>
  <div class="article-width">
    <ArticlePage
      :list="articleList"
      title=""
      :total="total"
      :current-page="query.page"
      @current-change="handleCurrentChange"
    />
  </div>
</template>
<script>
import ArticlePage from "@/components/contents/library/ArticlePage.vue";
import services from "@/services";
export default {
  name: "notice",

  components: {
    ArticlePage
  },

  data() {
    return {
      total: 0,
      query: {
        page: 1,
        pageSize: 10
      },
      articleList: []
    };
  },

  created() {
    this.getData();
  },

  methods: {
    getData() {
      services
        .getArticleList({ ...this.query, type: 3 })
        .then(res => {
          this.articleList = res.data.data.records.map(item => {
            item.type = "zaipei";
            return item;
          });
          this.total = res.data.data.total;
        })
        .catch(err => {
          this.$msgbox({
            type: "error",
            message: err
          });
        });
    },

    handleCurrentChange() {
      this.getData();
    }
  }
};
</script>
