<template>
  <div class="record">
    <el-form :model="formData" label-width="80px" ref="form" size="small">
      <el-form-item prop="userName" label="" :rules="formDataRules.userName">
        <el-input placeholder="" v-model="formData.userName"></el-input>
      </el-form-item>
      <el-form-item prop="phone" label="" :rules="formDataRules.phone">
        <el-input placeholder="" v-model="formData.phone"></el-input>
      </el-form-item>
      <el-form-item prop="address" label="" :rules="formDataRules.address">
        <el-input placeholder="" v-model="formData.address"></el-input>
      </el-form-item>
      <el-form-item prop="amount" label="" :rules="formDataRules.amount">
        <el-input placeholder="" v-model.number="formData.amount"></el-input>
      </el-form-item>
      <el-form-item prop="applyReason" label="" :rules="formDataRules.applyReason">
        <el-input
          placeholder=""
          v-model="formData.applyReason"
          type="textarea"
          :autosize="autosize"
        ></el-input>
      </el-form-item>
      <!-- <el-form-item v-if="0" prop="fileList" label="" :rules="formDataRules.fileList">
        <el-upload
          :action="getUploadUrl()"
          :with-credentials="true"
          list-type="picture-card"
          :file-list="formData.fileList"
          :on-success="handleAvatarSuccess"
          :on-remove="handleRemove"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
      </el-form-item>-->
    </el-form>

    <div class="footer">
      <el-button @click="handBack" size="small"> </el-button>
      <el-button @click="handleReset" size="small"> </el-button>
      <el-button @click="handleSave" size="small" type="primary"> </el-button>
    </div>
  </div>
</template>
<script>
import services from "@/services";
export default {
  name: "edit",
  props: {
    detail: {
      required: true,
      type: Object
    }
  },
  data() {
    const validatePhone = (rules, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }

      let reg = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/;
      if (!reg.test(value)) {
        return callback(new Error(""));
      }
      callback();
    };
    return {
      baseURL: services.file.baseURL,
      formData: this.detail,
      id: this.detail.id,

      formDataRules: {
        userName: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        phone: [
          {
            validator: validatePhone,
            required: true,
            trigger: "change"
          }
        ],
        address: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        amount: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            type: "number",
            message: "",
            trigger: "change"
          }
        ],
        applyReason: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      },

      autosize: {
        minRows: 6,
        maxRows: 10
      }
    };
  },

  methods: {
    getUploadUrl() {
      return services.file.uploadUrl;
    },

    handleAvatarSuccess(res, file, fileList) {
      file.url = this.baseURL + res.data;
      this.formData.fileList = fileList;
    },

    handleRemove(file) {
      let index = this.formData.fileList.indexOf(file);
      this.formData.fileList.splice(index, 1);
    },

    handBack() {
      this.$emit("close");
    },

    handleSave() {
      this.$refs.form.validate(valid => {
        if (!valid) {
          return;
        }

        let data = {
          id: this.formData.id,
          userName: this.formData.userName,
          phone: this.formData.phone,
          address: this.formData.address,
          amount: this.formData.amount,
          applyReason: this.formData.applyReason
        };
        services
          .modifyApply(data)
          .then(res => {
            if (res.data.code !== 0) {
              return Promise.reject(res.data.msg);
            }
            this.$msgbox({
              type: "success",
              message: ""
            });
            this.handBack();
          })
          .catch(err => {
            this.$msgbox({
              type: "error",
              message: err
            });
          });
      });
    },

    handleReset() {
      this.$refs["form"].resetField();
    }
  }
};
</script>
<style lang="scss" scoped>
.record {
  padding: 0 32px;
}

.footer {
  margin-top: 30px;
  text-align: center;
  min-width: 74px;
}
</style>