<template>
  <div class="article-page">
    <div class="article-page_title">{{title}}</div>
    <ArticleList :list="list"/>
    <div class="clearfix" v-show="total > 0">
      <el-pagination class="pagenation right" background @current-change="handleCurrentChange" :current-page="currentPage"
        :page-size="pageSize" layout="prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
  import ArticleList from '@/components/contents/library/ArticleList.vue';
  export default {
    name: "ArticlePage",

    components: {
      ArticleList
    },

    props: {
      title: {
        type: String,
        default: ""
      },

      list: {
        type: Array,
        default: () => []
      },

      total: {
        type: Number,
        default: 0
      },

      currentPage: {
        type: Number,
        default: 1
      },

      pageSize: {
        type: Number,
        default: 20
      }
    },

    data() {
      return {};
    },

    methods: {
      handleCurrentChange(currentPage) {
        this.$emit('currentChange', currentPage)
      }
    }
  }
</script>
<style lang="scss" scoped>
  .article-page {
    overflow: hidden;
  }

  .article-page_title {
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    margin: 30px 0;
  }

    .pagenation{
      margin-top: 24px;
      padding: 0 20px;
  }
</style>