<template>
  <div class="article-width wrap">
    <el-tabs tab-position="left tabs" style="height: 200px;" @tab-click="recordClick">
      <el-tab-pane label="">
        <edit />
      </el-tab-pane>
      <el-tab-pane label="">
        <record ref="record"/>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import record from "./record";
import edit from "./edit";
export default {
  name: "Application",

  components: {
    record,
    edit
  },

  data() {
    return {};
  },

  methods: {
    recordClick(vm) {
      if (vm.paneName === '1') {
        this.$refs.record.getData()
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.wrap {
  padding: 24px 32px;
}

.tabs {
  height: 100% !important;
  overflow: auto;
}
</style>