const state = {
  activeIndex: localStorage.getItem('activeIndex') || "/home",
  menus: iterationMenu()
}

const getters = {
  ActiveIndex(state) {
    return state.activeIndex
  },
  menus(state) {
    return state.menus
  },
}

const mutations = {
  setActiveIndex(state, index) {
    state.activeIndex = index
    localStorage.setItem('activeIndex', index)
  },

  iterationMenu(state, role) {
    state.menus = iterationMenu(role)
  }
}

function iterationMenu(roleParam) {

  let role = roleParam || +localStorage.getItem('userType')
  let menuArr = []
  let role1 = [
    {
      label: "",
      route: {
        path: "/ArticleManage"
      }
    },
    {
      label: "",
      route: {
        path: "/ArticlePublish"
      }
    }
  ]

  let role2 = role1

  let role3 = [{
    label: "",
    route: {
      path: "/Application"
    }
  },
  {
    label: "",
    route: {
      path: "/Mine"
    }
  }]

  let commonMenu = [{
    label: "",
    route: {
      path: "/home"
    }
  },
  {
    label: "",
    route: {
      path: "/articleSearch"
    }
  },
  {
    label: "",
    route: {
      path: "/bingchongArticle"
    }
  },
  {
    label: "",
    route: {
      path: "/zaipeiArticle"
    }
  },
  {
    label: "",
    route: {
      path: "/notice"
    }
  },
  {
    label: "",
    route: {
      path: "/Summary"
    }
  },
  ]

  if (role === 1) {
    menuArr = commonMenu.concat(role1)
  } else if (role === 2) {
    menuArr = commonMenu.concat(role2)
  } else {
    menuArr = commonMenu.concat(role3)
  }

  return menuArr
}


export default {
  state,
  getters,
  mutations
}