<template>
	<article class="article-width article-search">
		<header class="search-wrap">
			<el-input
				v-model="titleSearch"
				suffix-icon="el-icon-search"
				placeholder=""
			></el-input>
		</header>
		<ArticleList :list="articleList" />
		<div class="clearfix" v-show="total > 0">
			<el-pagination
				class="pagenation right"
				background
				@current-change="handleCurrentChange"
				:current-page.sync="query.page"
				:page-size="20"
				layout="prev, pager, next, jumper"
				:total="total"
			></el-pagination>
		</div>
	</article>
</template>
<script>
	import services from "@/services";
	import ArticleList from "@/components/contents/library/ArticleList";
	export default {
		name: "ArticleSearch",

		components: {
			ArticleList
		},

		data() {
			return {
				total: 0,
				query: {
					page: 1,
					pageSize: 10
				},
				titleSearch: "",
				timer: null,
				articleList: []
			};
		},

		watch: {
			titleSearch() {
				clearTimeout(this.timer);
				this.timer = setTimeout(() => {
					this.getData();
				}, 300);
			}
		},

		methods: {
			getData() {
				services
					.getSearchArticleList({
						...this.query,
						content: this.titleSearch
					})
					.then(res => {
						if (res.data.code === 0) {
							let data = res.data.data;
              this.articleList = data.records.map(item=>{
                if (item.type === 2) {
                  item.type = 'bingchong'
                }else if (item.type === 3) {
                  item.type = 'zaipei'
                }
                return item
              });
							this.total = data.total;
						} else {
							this.$message.error(res.data.msg);
						}
					}, 300)
					.catch(err => {
						this.$msgbox({
							type: "error",
							message: err
						});
					});
			},

			handleCurrentChange() {
				this.getData();
			}
		}
	};
</script>
<style lang="scss" scoped>
	@import "~@/scss/variables.scss";

	.article-search {
		padding: 24px 60px;
	}

	.search-wrap {
		margin-bottom: 30px;
		padding: 0 30px;
	}

	.pagenation {
		margin-top: 24px;
		padding: 0 20px;
	}
</style>