<template>
	<div class="article-width">
		<ArticlePage
			:list="articleList"
			title=""
			:total="total"
			:current-page="query.page"
			@current-change="handleCurrentChange"
		/>
	</div>
</template>
<script>
	import ArticlePage from "@/components/contents/library/ArticlePage.vue";
	import services from "@/services";
	export default {
		name: "notice",

		components: {
			ArticlePage
		},

		data() {
			return {
				total: 100,
				query: {
					page: 1,
					pageSize: 10
				},
				articleList: []
			};
		},

		created() {
			this.getData();
		},

		methods: {
			getData() {
				services
					.getSummaryArticleList(this.query)
					.then(res => {
						if (res.data.code !== 0) {
							return Promise.reject(res.code.msg);
						}
						let data = res.data.data;
						this.articleList = data.records.map(item => {
							item.type = "summary";
							return item;
						});
						this.total = data.total;
					})
					.catch(err => {
						this.$msgbox({
							type: "error",
							message: err
						});
					});
			},

			handleCurrentChange(val) {
				this.query.page = val;
				this.getData();
			}
		}
	};
</script>
