<template>
  <div class="article-publish article-width">
    <div class="coverImg right">
      <span class="coverImg-label"></span>
      <el-upload
        class="avatar-uploader"
        :action="getUploadUrl()"
        :show-file-list="false"
        :on-success="handleAvatarSuccess"
        :before-upload="beforeAvatarUpload"
      >
        <img v-if="formData.coverImg" :src="formData.coverImg" class="avatar" />
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
    </div>
    <div class="left">
      <el-form :model="formData" label-width="80px" ref="form" class="form">
        <el-form-item label="" prop="category" :rules="rules.category">
          <el-select v-model="formData.category" class="form-field" placeholder="">
            <el-option
              v-for="(option) in articleOptions"
              :key="option.value"
              :label="option.label"
              :value="option.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="title" :rules="rules.title">
          <el-input class="form-filed" v-model="formData.title" placeholder=""></el-input>
        </el-form-item>
        <!-- <el-form-item label="" prop="authorName" :rules="rules.authorName">
          <el-input class="form-filed" v-model="formData.authorName" placeholder=""></el-input>
        </el-form-item>-->
      </el-form>
    </div>

    <div class="editor-wrap clearfix">
      <div ref="editor" class="editor"></div>
    </div>

    <div class="footer">
      <el-button @click="handleReset"> </el-button>
      <el-button type="primary" @click="handleSave"> </el-button>
    </div>
  </div>
</template>
<script>
import WangEditor from "wangeditor";
import services from "@/services";
import { mapGetters } from "vuex";
export default {
  name: "ArticlePublish",

  props: {
    id: {
      type: String,
      default: ""
    },

    type: {
      type: String,
      default: ""
    }
  },

  data() {
    return {
      baseURL: services.file.baseURL,
      editor: null,
      formData: {
        category: null,
        title: null,
        authorName: null,
        content: null,
        coverImg: null
      },
      rules: {
        category: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        title: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        authorName: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },

  computed: {
    ...mapGetters(["articleOptions"])
  },

  mounted() {
    this.initEditor();
    this.getDetail();
  },

  methods: {
    getUploadUrl() {
      return services.file.uploadUrl;
    },

    showError(err) {
      this.$msgbox({
        type: "error",
        message: err
      });
    },

    handleAvatarSuccess(res) {
      this.formData.coverImg = this.baseURL + res.data;
    },

    beforeAvatarUpload(file) {
      const isJPG =
        ["image/jpeg", "image/png", "image/jpg", "image/bmp"].indexOf(
          file.type
        ) !== -1;
      const isLt2M = file.size / 1024 / 1024 < 1;

      if (!isJPG) {
        this.$message.error(" JPG/JPEG/PNG !");
      }
      if (!isLt2M) {
        this.$message.error(" 1MB!");
      }
      return isJPG && isLt2M;
    },

    handleReset() {
      this.$refs["form"].resetFields();

      this.editor.txt.html("<p><br></p>");
    },

    handleSave() {
      this.$refs["form"].validate(valid => {
        if (!valid) {
          return;
        }

        if (!this.formData.content || this.formData.content === "<p><br></p>") {
          this.$message.error("");
          return;
        }
        if (this.type === "bingchong" || this.type === "zaipei") {
          let type = this.type === "bingchong" ? 2 : 3;
          services
            .addArticle({
              id: this.id,
              type: type,
              title: this.formData.title,
              coverImg: this.formData.coverImg,
              content: this.formData.content
            })
            .then(res => {
              if (res.data.code === 0) {
                this.$msgbox({
                  type: "success",
                  message: ""
                });
              }
            })
            .catch(err => {
              this.showError(err);
            });
        } else if (this.type === "summary") {
          services
            .addSummary({
              id: this.id,
              time: new Date(),
              title: this.formData.title,
              coverImg: this.formData.coverImg,
              content: this.formData.content
            })
            .then(res => {
              if (res.data.code === 0) {
                this.$msgbox({
                  type: "success",
                  message: ""
                });
              }
            })
            .catch(err => {
              this.showError(err);
            });
        }else if (this.type === 'notice') {
          services
            .addNotice({
              id: this.id,
              title: this.formData.title,
              coverImg: this.formData.coverImg,
              content: this.formData.content
            })
            .then(res => {
              if (res.data.code === 0) {
                this.$msgbox({
                  type: "success",
                  message: ""
                });
              }
            })
            .catch(err => {
              this.showError(err);
            });
        }
      });
    },

    getDetail() {
      if (this.type === "notice") {
        services
          .getNoticeDetail(this.id)
          .then(res => {
            this.formData = res.data.data;
          })
          .catch(err => {
            this.showError(err);
          });
      } else if (this.type === "bingchong" || this.type === "zaipei") {
        services
          .getNoticeDetail(this.id)
          .then(res => {
            this.formData = res.data.data;
          })
          .catch(err => {
            this.showError(err);
          });
      } else if (this.type === "summary") {
        services
          .getSummaryDetail(this.id)
          .then(res => {
            this.formData = res.data.data;
          })
          .catch(err => {
            this.showError(err);
          });
      }
    },

    initEditor() {
      this.editor = new WangEditor(this.$refs["editor"]);

      let editor = this.editor;

      editor.customConfig.pasteFilterStyle = false;

      editor.customConfig.uploadImgServer = this.getUploadUrl();

      editor.customConfig.uploadImgMaxSize = 1 * 1024 * 1024;

      editor.customConfig.withCredentials = true;

      editor.customConfig.uploadImgHooks = {
        error: () => {
          // 
          // xhr  XMLHttpRequst editor 
          this.$message.error("");
        }
      };

      editor.customConfig.onchange = html => {
        //  textarea
        this.formData.content = html;
      };

      editor.create();
    }
  }
};
</script>
<style lang="scss" scoped>
.article-publish {
  padding: 0 20px;
  border-bottom: 1px solid transparent;

  .coverImg {
    margin-top: 24px;
  }

  .coverImg-label {
    display: inline-block;
    margin-bottom: 10px;
    color: rgba(0, 0, 0, 0.65);
  }

  .form {
    width: 600px;
    margin-top: 24px;
  }

  .form-field {
    width: 100%;
  }
}

.editor-wrap {
  padding-top: 24px;
}

.footer {
  margin-top: 24px;
  text-align: center;
  margin-bottom: 24px;
}
</style>