<template>
	<div class="login">
    <router-link tag="span" class="name" to="/home"></router-link>
		<div class="title"></div>
		<el-form :model="loginForm" ref="loginForm" label-width="80px">
			<el-form-item label="" prop="phone" :rules="rules.phone">
				<el-input placeholder="" class="form-field" v-model="loginForm.phone"></el-input>
			</el-form-item>
			<el-form-item label="" prop="password" :rules="rules.password">
				<el-input placeholder="" class="form-field" v-model="loginForm.password" type="password"></el-input>
			</el-form-item>
		</el-form>
		<el-button class="btn" type="primary" @click.enter="handleLogin"></el-button>

		<div class="footer">
			
			<router-link to="/register"></router-link>
		</div>
	</div>
</template>
<script>
	import services from "@/services";
	import { mapActions } from "vuex";
	export default {
		name: "Login",
		data() {
			return {
				loginForm: {
					phone: "",
					password: ""
				},

				rules: {
					phone: [{ required: true, message: "", trigger: "blur" }],
					password: [{ required: true, message: "", trigger: "blur" }]
				}
			};
		},

		methods: {
      ...mapActions(['actionLogin']),
			handleLogin() {
				this.$refs["loginForm"].validate(valid => {
					if (!valid) {
						return;
					}
					services
						.login(this.loginForm)
						.then(res => {
							if (res.data.code !== 0) {
								this.$message.error("");
							} else {
                console.log("handleLogin -> res.data.data", res.data.data)
								this.actionLogin(res.data.data);
								this.$message.success("");
								this.$router.push("/home");
							}
						})
						.catch(err => {
							this.$message.error(err);
						});
				});
			}
		}
	};
</script>
<style lang="scss" scoped>
	.login {
		width: 400px;
		margin: 20vh auto 0;
		text-align: center;
	}

	.name {
		font-size: 28px;
		margin-bottom: 50px;
		color: rgba(0, 0, 0, 0.45);
    display: block;
	}

	.title {
		font-weight: bold;
		font-size: 16px;
		margin-bottom: 20px;
	}

	.form-field {
		width: 300px;
	}

	.btn {
		@extend .form-field;
		margin-left: 80px;
	}

	.footer {
		margin-top: 30px;
		padding-top: 20px;
		border-top: 1px solid #ccc;
	}
</style>