import axios from 'axios';
// import router from '../route/index';
// import { message } from 'element-ui';
import store from '@/store';
import router from '@/route';
// import { Message } from 'element-ui'

let userType = store.getters.userType

axios.defaults.baseURL = 'http://124.156.99.143:8099';
axios.defaults.withCredentials = true

// 
axios.interceptors.request.use(function (config) {
  config.headers.token = store.getters.token
  // 
  return config;
}, function (error) {
  // 
  return Promise.reject(error);
});

// 
axios.interceptors.response.use(function (response) {
  // 
  if (response.data.code === 401) {
    store.commit({
      type: 'logout'
    })
    response.data.msg = ''
    router.push('/home')
  }
  return response;
}, function (error) {

  // 
  return Promise.reject(error);
});

const api = {
  file: {
    uploadUrl: axios.defaults.baseURL + '/common/upload',
    baseURL: axios.defaults.baseURL
  },
  register(data) {
    let url = '/common/register'
    return axios.post(url, data)
  },
  login(data) {
    let url = '/common/login'
    return axios.post(url, data)
  },
  getSummaryArticleList(data) {
    let url = '/common/sumup'
    return axios.get(url, {
      params: data
    })
  },
  addSummary(data) {
    let url = '/tech/sumup'
    if (userType === 1) {
      url = '/admin/sumup'
    }
    return axios.post(url, data)
  },
  getSummaryDetail(id) {
    let url = '/common/sumup-detail'
    return axios.get(url, {
      params: {
        id: id
      }
    })
  },
  getSearchArticleList(query) {
    let url = "/common/article/search"
    return axios.get(url, {
      params: query
    })
  },
  getNoticeArticleList(query) {
    let url = "/common/notice"
    return axios.get(url, {
      params: query
    })
  },
  getArticleList(data) {
    let url = '/common/article'
    return axios.get(url, {
      params: data
    })
  },

  addNotice(data) {
    let url = "/admin/notice"
    return axios.post(url, data)
  },
  getNoticeDetail(id) {
    let url = "/common/notice-detail"
    return axios.get(url, {
      params: {
        id
      }
    })
  },
  getArticleDetail(id) {
    let url = "/common/article-detail"
    return axios.get(url, {
      params: {
        id
      }
    })
  },

  addApply(data) {
    let url = '/user/apply'
    return axios.post(url, data)
  },

  applyList(data) {
    let url = '/user/apply'
    return axios.get(url, { params: data })
  },
  deleteApply(id) {
    let url = '/user/apply'
    return axios.delete(url, { params: { id } })
  },
  modifyApply(data) {
    let url = '/user/apply'
    return axios.put(url, data)
  },

  addComment(data) {
    let url = '/user/comment'
    return axios.post(url, data)
  },
  addCollect(articleId) {
    let url = "/user/collect/" + articleId
    return axios.post(url)
  },
  deleteCollect(articleId) {
    let url = "/user/collect/" + articleId
    return axios.delete(url)
  },

  addArticle(data) {
    let url = '/tech/article'
    return axios.post(url, data)
  },
  deleteArticle(id) {
    let url = '/tech/article'
    return axios.delete(url, { params: { id } })
  },
  deleteSummary(id) {
    let url = '/tech/article/' + id
    return axios.delete(url)
  },
  deleteNotice(id) {
    let url = '/admin/notice/' + id
    return axios.delete(url)
  },

  getComment(data) {
    let url = '/user/comment/' + data.id
    return axios.get(url)
  },

  modifyPassword(data) {
    let url = ""
    return axios.post(url, data)
  },

  getMyCollect() { }

}

export default api