const state = {
  articleOptions: getArticleOptions() || [],
}

const getters = {
  articleOptions(state) {
    return state.articleOptions
  }
}

const mutations = {
  setArticleOptionsByRole(state, role) {
    getArticleOptions(role)
  }
}

function getArticleOptions(role2) {
  let role = role2 || +localStorage.getItem('userType')
  let articleOptions = []
  //1  2 3
  if (role === 1) {
    articleOptions = [
      {
        label: "",
        value: "notice"
      },
      {
        label: "",
        value: "summary"
      }]
  } else {
    articleOptions = [{
      label: "",
      value: "bingchong"
    },
    {
      label: "",
      value: "zaipei"
    },
    {
      label: "",
      value: "summary"
    }]
  }

  return articleOptions
}

export default {
  state,
  getters,
  mutations
}