const state = {
  isLogin: localStorage.getItem('token'),
  token: localStorage.getItem('token'),
  userType: +localStorage.getItem('userType'),
  roleDesc: localStorage.getItem('roleDesc'),
}

const getters = {
  isLogin(state) {
    return state.isLogin
  },
  userType(state) {
    return state.userType
  },
  token(state) {
    return state.token
  }
}


const mutations = {
  setLoginStatus(state, payload) {
    state.isLogin = !!payload.token
    state.token = payload.token
    state.userType = payload.userType
    state.roleDesc = payload.roleDesc
    localStorage.setItem('token', payload.token)
    localStorage.setItem('userType', payload.userType)
    localStorage.setItem('roleDesc', payload.roleDesc)
  },
  logout(state) {
    state.isLogin = false
    state.token = ''
    state.userType = ''
    state.roleDesc = ''
    localStorage.removeItem('token')
    localStorage.removeItem('userType')
    localStorage.removeItem('roleDesc')
  }
}

const actions = {
  actionLogin({ commit }, payload) {
    commit('setLoginStatus', payload)
    commit('setActiveIndex', '/home')
    commit('iterationMenu', +payload.userType)
    commit('setArticleOptionsByRole', +payload.userType)
  }
}


export default {
  state,
  getters,
  mutations,
  actions
}