<template>
	<div class="login">
		<div class="name"></div>
		<div class="title"></div>
		<el-form :model="registerForm" ref="form" label-width="80px">
			<el-form-item label="" prop="name" :rules="rules.name">
				<el-input placeholder="" maxlength="10" :show-word-limit="true" class="form-field" v-model="registerForm.name"></el-input>
			</el-form-item>
			<el-form-item label="" prop="phone" :rules="rules.phone">
				<el-input placeholder="" class="form-field" v-model="registerForm.phone" type="text"></el-input>
			</el-form-item>
			<el-form-item label="" prop="password" :rules="rules.password">
				<el-input
					placeholder=""
					class="form-field"
					v-model="registerForm.password"
					type="password"
				></el-input>
			</el-form-item>
			<el-form-item label="" prop="password2" :rules="rules.password2">
				<el-input
					placeholder=""
					class="form-field"
					v-model="registerForm.password2"
					type="password"
				></el-input>
			</el-form-item>
			<el-form-item label="" prop="cardId" :rules="rules.cardId">
				<el-input placeholder="" class="form-field" v-model="registerForm.cardId" type="text"></el-input>
			</el-form-item>
		</el-form>
		<el-button class="btn" type="primary" @click="handleRegister"></el-button>
		<div class="footer">
			
			<router-link to="/login"></router-link>
		</div>
	</div>
</template>
<script>
	import services from "@/services";
	export default {
		name: "Register",
		data() {
			const validatePhone = (rules, value, callback) => {
				if (!value) {
					return callback(new Error(""));
				}

				let reg = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/;
				if (!reg.test(value)) {
					return callback(new Error(""));
				}
				callback();
			};
			const validateCardId = (rules, value, callback) => {
				let reg = /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/;
				if (value && !reg.test(value)) {
					return callback(new Error(""));
				}

				callback();
			};
			const validatePassword2 = (rules, value, callback) => {
				if (!value) {
					return callback(new Error(""));
				}
				if (value.length < 6 || value.length > 18) {
					return callback(new Error("6-18"));
				}
				if (value && this.registerForm.password !== value) {
					return callback(new Error(""));
				}

				callback();
			};
			return {
				registerForm: {
					name: "",
					phone: "",
					password: "",
					password2: "",
					cardId: ""
				},

				rules: {
					password: [
						{
							required: true,
							message: "6-18",
							min: 6,
							max: 18,
							trigger: "blur"
						}
					],
					password2: [
						{
							required: true,
							validator: validatePassword2,
							trigger: "blur"
						}
					],
					phone: [
						{
							validator: validatePhone,
							required: true,
							trigger: "change"
						}
					],
					cardId: [{ validator: validateCardId, trigger: "change" }]
				}
			};
		},

		methods: {
			handleRegister() {
				this.$refs["form"].validate(valid => {
					if (!valid) {
						return;
					}

					if (this.registerForm.password !== this.registerForm.password2) {
						this;
					}

					const data = {
						name: this.registerForm.name,
						phone: this.registerForm.phone,
						password: this.registerForm.password,
						cardId: this.registerForm.cardId
					};
					services
						.register(data)
						.then(res => {
							console.log("handleRegister -> res", res);
							if (res.data.code !== 0) {
								this.$message.error(res.data.msg);
							} else {
								this.$message.success("");
								this.$router.push("/login");
							}
						})
						.catch(err => {
							this.$message.error(err);
						});
				});
			}
		}
	};
</script>
<style lang="scss" scoped>
	.login {
		width: 400px;
		margin: 20vh auto 0;
		text-align: center;
	}

	.name {
		font-size: 28px;
		margin-bottom: 50px;
		color: rgba(0, 0, 0, 0.45);
	}

	.title {
		font-weight: bold;
		font-size: 16px;
		margin-bottom: 20px;
	}

	.form-field {
		width: 300px;
	}

	.btn {
		@extend .form-field;
		margin-left: 80px;
	}

	.footer {
		margin-top: 30px;
		padding-top: 20px;
		border-top: 1px solid #ccc;
	}
</style>