<template>
  <div class="records">
    <div class="list" v-show="!isReedit">
      <el-card v-for="(item, index) in list" :key="index">
        <div slot="header" class="clearfix">
          <el-button style="padding: 3px 0" type="text" @click="handleDelete(item)"></el-button>
          <span class="split">|</span>
          <el-button style="padding: 3px 0" type="text" @click="handleModify(item)"></el-button>
        </div>
        <div class="box-card">
          <div class="flex-item">{{item.userName}}</div>
          <div class="flex-item">
            <el-timeline>
              <el-timeline-item :color="item.status | colorFilter">{{item.status | statusFilter}}</el-timeline-item>
            </el-timeline>
          </div>
          <div class="flex-item">{{item.phone}}</div>
          <div class="flex-item amount">{{item.amount}}</div>
          <div class="flex-item">{{item.address}}</div>
        </div>
      </el-card>
    </div>
    <div class="clearfix" v-show="total > 0 && !isReedit">
      <el-pagination
        class="pagenation right"
        background
        @current-change="handleCurrentChange"
        :current-page="query.page"
        :page-size="query.pageSize"
        layout="prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
    <reedit :detail="detail" v-if="isReedit" @close="closeReedit" />
  </div>
</template>
<script>
import services from "@/services";
import reedit from "./reedit";
export default {
  name: "record",
  components: {
    reedit
  },
  data() {
    return {
      detail: {},
      isReedit: false,
      list: [
        {
          id: 12,
          userId: 3,
          userName: "",
          phone: "13799999999",
          address: "",
          amount: "",
          applyReason: "",
          content: null,
          status: 1,
          statusDesc: "",
          authName: null,
          refuseDesc: null,
          createTime: "2020-04-08T02:31:30.000+0000",
          updateTime: "2020-04-08T02:31:30.000+0000"
        }
      ],
      total: 0,
      query: {
        page: 1,
        pageSize: 10
      }
    };
  },

  filters: {
    statusFilter(value) {
      //123',
      let label = "";
      switch (value) {
        case 2:
          label = "";
          break;
        case 3:
          label = "";
          break;
        default:
          label = "";
      }

      return label;
    },

    colorFilter(value) {
      let color = "#E6A23C";
      switch (value) {
        case 2:
          color = "#F56C6C";
          break;
        case 3:
          color = "#67C23A";
          break;
        default:
          color = "#E6A23C";
      }

      return color;
    }
  },

  methods: {
    getData() {
      services
        .applyList(this.query)
        .then(res => {
          if (res.data.code !== 0) {
            return Promise.reject(res.data.msg);
          }
          this.total = res.data.data.total;
          this.list = res.data.data.records;
        })
        .catch(err => {
          this.$msgbox({
            type: "error",
            message: err
          });
        });
    },

    handleCurrentChange(val) {
      this.query.page = val;
      this.getData();
    },

    closeReedit() {
      this.isReedit = false;
    },

    handleDelete(item) {
      this.$msgbox({
        type: "warning",
        message: "",
        showCancelButton: true
      })
        .then(() => {
          services
            .deleteApply(item.id)
            .then(res => {
              if (res.data.code === 0) {
                this.$message({
                  type: "success",
                  message: ""
                });
                this.getData();
              }
            })
            .catch(err => {
              this.$msgbox({
                type: "error",
                message: err
              });
            });
        })
        .catch(() => {
          return;
        });
    },

    handleModify(item) {
      this.detail = item;
      this.isReedit = true;
    }
  }
};
</script>
<style lang="scss" scoped>
.records {
  padding: 24px 32px;
}

.box-card {
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
  color: rgba(0, 0, 0, 0.65);
}

.flex-item {
  width: 50%;
}

.amount {
  padding-left: 40px;
  margin-bottom: 20px;
}
</style>