<template>
  <div class="navbar-wrap">
    <el-menu :default-active="ActiveIndex" class="navbar-content" background-color="#67C23A" text-color="#fff"
      active-text-color="#111" :router="true" mode="horizontal" @select="handleSelect">
      <el-menu-item v-for="(item, index) in menus" :key="index" :index="item.route.path" :route="item.route">
        {{item.label}}
      </el-menu-item>
    </el-menu>
  </div>
</template>
<script>
  import { mapGetters, mapMutations } from 'vuex';

  export default {
    name: "Navbar",
    data() {
      return {
        
      };
    },

    computed:{
      ...mapGetters(['ActiveIndex', 'menus'])
    },

    watch:{
      $route(newVal){
        if(this.menus.indexOf(newVal.path) !== -1){
          this.setActiveIndex(newVal.path)
        }
      }
    },

    methods:{
      ...mapMutations(['setActiveIndex']),
      handleSelect(index){
        this.setActiveIndex(index)
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/scss/variables.scss";

  .navbar-content {
    margin: 0 auto;
  }

  .navbar-wrap{
    margin-right: 90px;
  }
</style>