<template>
    <div class="article-list">
        <div class="list-header">
            <span class="list-header_title">{{title}}</span>
            <span class="right more">
                <router-link tag="span" :to="opretionBtn.path">{{opretionBtn.label}}
                    <i class="el-icon-d-arrow-right"></i>
                </router-link>
            </span>
        </div>
        <div class="list-content">
            <div v-for="(item, index) in list" :key="index" class="list-content_item">
                <router-link tag="span" :to="item.id">{{item.title}}</router-link>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "ArticleList",
        props: {
            title: {
                type: String,
                default: ""
            },

            opretionBtn: {
                type: Object,
                default: () => ({
                    path: "",
                    label: ""
                })
            },

            list: {
                type: Array,
                required: true,
                default: () => []
            }
        },

        data() {
            return {};
        }
    }
</script>

<style lang="scss" scoped>
    @import "~@/scss/variables.scss";

    .article-list {
        width: 400px;
        border: 1px solid #999;
        background: #fff;
    }

    .list-header {
        background: $theme-color;
        color: #fff;
        padding: 10px 10px 10px 20px;
        font-size: 16px;
    }

    .list-content {
        height: 228px;
    }

    .list-content_item {
        padding: 10px 10px;
        font-size: 14px;
        border: 1px dashed #eee;
        cursor: pointer;
    }

    .more {
        cursor: pointer;
    }
</style>