<template>
  <div class="article-detail article-width">
    <el-page-header @back="goBack" class="page-header" content=""></el-page-header>

    <div class="article-content">
      <div class="field-row title">
        <span>{{articleDetail.title}}</span>
      </div>
      <div class="field-row author clearfix">
        <span class="left">{{articleDetail.authorName}}</span>
        <span class="right">{{articleDetail.createTime}}</span>
      </div>
      <!-- <div class="field-row img" v-if="articleDetail.coverImg">
        <img :src="articleDetail.coverImg" alt />
      </div>-->
      <div class="field-row">
        <div class="content" v-html="articleDetail.content"></div>
      </div>

      <div class="collect-btn">
        <el-button type="primary" :icon="collectIcon" @click="handleCollect"></el-button>
      </div>
    </div>

    <div class="comment" v-if="showComent">
      <div class="add-comment">
        <el-input type="textarea" :autosize="autosize" v-model="CommentContent"></el-input>
        <div class="add-comment-btn">
          <el-button @click="addComment" type="primary" size="small"></el-button>
        </div>
      </div>
      <div class="comment-item" v-for="(comment, index) in commentList" :key="index">
        <div class="comment-header">
          <span class="name">{{comment.userName}}</span>
          <span class="time">{{comment.createTime}}</span>
        </div>
        <div class="comment-content">{{comment.content}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import services from "@/services";
import { mapGetters } from "vuex";
export default {
  name: "ArticleDetail",

  props: {
    type: {
      type: String,
      required: true
    },
    id: {
      type: [String, Number],
      required: true
    }
  },

  data() {
    return {
      collectIcon: "el-icon-star-off",
      articleDetail: {
        authorName: null,
        // coverImg: null,
        title: null,
        content: null,
        createTime: null
      },
      CommentContent: "",
      commentList: [
        // {
        //   userName: 123,
        //   createTime: 123,
        //   content: 123
        // }
      ],
      commentQuery: {
        page: 1,
        pageSize: 10
      },
      autosize: {
        minRows: 4,
        maxRows: 4
      }
    };
  },

  computed: {
    ...mapGetters(["userType"]),
    showComent() {
      let type = this.type;
      if (
        (type === "bingchong" || type === "zaipei" || type === "summary") &&
        this.userType === 3
      ) {
        return true;
      }

      return false;
    }
  },

  created() {
    this.getDetail();
    this.showComent && this.getComment();
  },

  methods: {
    goBack() {
      this.$router.go(-1);
    },

    showError(err) {
      this.$msgbox({
        type: "error",
        message: err
      });
    },

    getDetail() {
      if (this.type === "notice") {
        services
          .getNoticeDetail(this.id)
          .then(res => {
            this.articleDetail = res.data.data;
          })
          .catch(err => {
            this.showError(err);
          });
      } else if (this.type === "bingchong" || this.type === "zaipei") {
        services
          .getArticleDetail(this.id)
          .then(res => {
            this.articleDetail = res.data.data;
          })
          .catch(err => {
            this.showError(err);
          });
      } else if (this.type === "summary") {
        services
          .getSummaryDetail(this.id)
          .then(res => {
            this.articleDetail = res.data.data;
          })
          .catch(err => {
            this.showError(err);
          });
      }
    },

    getComment() {
      services
        .getComment({ id: this.id })
        .then(res => {
          this.commentList = res.data.data;
          // userName":"",
          //   "content":"222",
          //   "createTime":"2020-04-08T14:31:52",
          //   "updateTime":"2020-04-08T14:35:32",
        })
        .catch(err => {
          this.showError(err);
        });
    },

    addComment() {
      services
        .addComment({
          articleId: +this.id,
          content: this.CommentContent
        })
        .then(res => {
          if (res.data.code === 0) {
            return;
          }
        })
        .catch(err => {
          this.showError(err);
        });
    },

    handleCollect() {
      if (this.collectIcon === "el-icon-star-off") {
        //
        services
          .addCollect(this.id)
          .then(res => {
            if (res.data.code === 0) {
              this.collectIcon === "el-icon-star-on";
              return;
            }
          })
          .catch(err => {
            this.showError(err);
          });
      } else {
        //
        services
          .deleteCollect(this.id)
          .then(res => {
            if (res.data.code === 0) {
              this.collectIcon === "el-icon-star-off";
              return;
            }
          })
          .catch(err => {
            this.showError(err);
          });
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.fontsColor {
  color: rgba(0, 0, 0, 0.45);
  font-size: 14px;
}
.article-detail {
  padding: 24px 32px;
}

.page-header {
  border-bottom: 1px solid #eee;
  padding-bottom: 16px;
}

.article-content {
}

.avatar {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
}

.field-row {
  line-height: 1.5;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.85);
  margin-top: 24px;
}

.title {
  font-size: 18px;
  text-align: center;
}

.author,
.time {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.45);
  padding: 0 20px;
}

.img {
  text-align: center;
}

.content {
  display: inline-block;
  color: rgba(0, 0, 0, 0.75);
}

.collect-btn {
  text-align: center;
}
.comment {
  margin-top: 30px;
  border-bottom: 1px dashed rgba(0, 0, 0, 0.45);
}

.comment-item {
  border-top: 1px dashed rgba(0, 0, 0, 0.45);
  margin-top: 10px;
  .comment-header {
    display: flex;
    justify-content: space-between;
    padding: 3px 10px;
  }
  .name {
    @extend .fontsColor;
  }

  .time {
    @extend .fontsColor;
  }

  .comment-content {
    padding: 3px 10px;
    color: rgba(0, 0, 0, 0.85);
  }
}

.add-comment-btn {
  margin-top: 20px;
  text-align: right;
}
</style>