<template>
	<section class="article-list">
		<div class="article-list_item" v-for="(article, index) in list" :key="index">
			<div class="title">
				<router-link tag="span" :to="getPath(article)">{{article.title}}</router-link>
			</div>
			<div class="info">
				<span class="author">{{article.authorName}}</span>
				<span class="time">{{article.createTime}}</span>
			</div>
		</div>
	</section>
</template>
<script>
	export default {
		name: "ArticleList",

		props: {
			list: {
				type: Array,
				default: () => []
			}
		},

		data() {
			return {};
		},

		methods: {
			getPath(article) {
				return "/articleDetail/" + article.type + "/" + article.id;
			}
		}
	};
</script>
<style lang="scss" scoped>
	@import "~@/scss/variables.scss";

	.article-list {
		padding: 0 20px;

		&_item {
			border-bottom: 1px dashed #ddd;
			padding: 5px 10px;
			position: relative;
			display: flex;
			flex-flow: row nowrap;

			.title {
				flex: 1 1 auto;
				width: 100%;
				font-size: 16px;
				line-height: 31px;
				cursor: pointer;

				&:hover {
					color: $hover-color;
					text-decoration: underline;
				}
			}

			.info {
				flex: 0 0 auto;
				width: 150px;
				font-size: 12px;
				color: rgba(0, 0, 0, 0.65);
				display: flex;
				flex-flow: column nowrap;
				text-align: right;

				.author {
					margin-bottom: 4px;
				}
			}
		}
	}
</style>