<template>
  <div class="wrap">
    <el-form :model="formData" :disabled="disabled" label-width="60px" label-position="left" ref="form">
      <el-form-item prop="name" label=":" :rules="rules.name">
        <el-input class="input" v-model="formData.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="phone" label=":" :rules="rules.phone">
        <el-input class="input" v-model="formData.phone" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="cardID" label=":" :rules="rules.oldPasscardIDword">
        <el-input class="input" v-model="formData.cardID" placeholder=""></el-input>
      </el-form-item>
    </el-form>

    <div class="footer">
      <el-button v-if="disabled" type="primary" @click="handleModify"></el-button>
      <template v-else>
        <el-button type="primary" @click="handleSave"></el-button>
        <el-button @click="reset"></el-button>
      </template>
    </div>
  </div>
</template>
<script>
  import services from '@/services';
  export default {
    name: "modifyPassword",
    data() {
      const validatorPhone = (rule, value, callback) => {
        if (!value) {
          callback()
        }
        const reg = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/
        if (reg.test(value)) {
          callback()
        } else {
          callback(new Error(''))
        }
      }

      const validatorID = (rule, value, callback) => {
        if (!value) {
          callback()
        }
        const reg =
          /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/
        if (reg.test(value)) {
          callback()
        } else {
          callback(new Error(''))
        }
      }
      return {
        disabled: true,
        formData: {
          name: null,
          phone: null,
          cardID: null,
        },

        rules: {
          name: [{
            message: "6-10",
            min: 2,
            max: 10,
            trigger: "change"
          }],
          phone: [{
            validator: validatorPhone,
            trigger: "change"
          }],
          cardID: [{
            validator: validatorID,
            trigger: "change"
          }]
        }
      };
    },

    methods: {
      handleSave() {
        this.$refs.form.validate(valid => {
          if (!valid) {
            return false
          }
          this.disabled = true
          services.modifyPassword()
        })
      },

      reset() {
        this.$refs.form.resetField()
      },

      handleModify() {
        this.disabled = false
      }
    }

  }
</script>
<style lang="scss" scoped>
  .wrap {
    padding: 24px 32px;
  }

  .input {
    width: 300px;
  }

  .footer {
    width: 400px;
    text-align: center;
  }
</style>