<template>
  <div class="collect">
    <ArticlePage :list="articleList" title="" :total="query.total" :current-page="query.currentPage"
      @current-change="handleCurrentChange" />
  </div>
</template>
<script>
  import ArticlePage from '@/components/contents/library/ArticlePage.vue';
   import services from '@/services';
  export default {
    name: "collect",

    components: {
      ArticlePage
    },

    data() {
      return {
        query: {
          total: 100,
          currentPage: 1
        },

        articleList: [{
            id: "234224",
            title: "",
            author: "zzzzzyyyy",
            createTime: "2020-03-20 22:22:22"
          },
          {
            id: "234234",
            title: "",
            author: "zzzzzyyyy",
            createTime: "2020-03-20 22:22:22"
          }
        ]
      };
    },

    created() {
      // this.getData()
    },

    methods: {
      getData() {
        services.getMyCollect(this.query)
          .then((res) => {
            this.articleList = res.data
          })
          .catch(err => {
            this.$msgbox({
              type: "error",
              message: err
            })
          })
      },

      handleCurrentChange() {
        this.getData()
      }
    }
  }
</script>
<style lang="scss" scoped>
.wrap{
  padding: 24px 32px;
}

.tabs{
  height: 100% !important;
  overflow: auto;
}
</style>