<template>
  <div class="wrap">
    <el-form :model="formData" label-width="100px" label-position="left">
      <el-form-item prop="oldPassword" label="" :rules="rules.oldPassword">
        <el-input type="password" class="input" v-model="formData.oldPassword" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="newPassword" label="" :rules="rules.oldPassword">
        <el-input type="password" class="input" v-model="formData.newPassword" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="newPassword2" label="" :rules="rules.oldPassword">
        <el-input type="password" class="input" v-model="formData.newPassword2" placeholder=""></el-input>
      </el-form-item>
    </el-form>

    <div class="footer">
      <el-button type="primary" @click="handleModifyPassword"></el-button>
      <el-button @click="reset"></el-button>
    </div>
  </div>
</template>
<script>
  import services from '@/services';
  export default {
    name: "modifyPassword",
    data() {
      return {
        formData: {
          oldPassword: null,
          newPassword: null,
          newPassword2: null,
        },

        rules: {
          oldPassword: [{
            required: true,
            message: "6-18",
            min: 6,
            max: 18,
            trigger: "change"
          }]
        }
      };
    },

    methods: {
      handleModifyPassword() {
        this.$refs.form.validate(valid => {
          if (!valid) {
            return false
          }
          if (this.formData.newPassword !== this.formData.newPassword2) {
            this.$message.error('')
            return
          }

          services.modifyPassword()
        })
      },

      reset() {
        this.$refs.form.resetField()
      }
    }

  }
</script>
<style lang="scss" scoped>
  .wrap {
    padding: 24px 32px;
  }

  .input {
    width: 300px;
  }

  .footer {
    width: 400px;
    text-align: center;
  }
</style>